import React from 'react';

const HistoryPage = () => {
    return (
        <div className="history">
            <h1 className="title">HISTORIA</h1>
            <br></br>
            <p>Korzenie FC Barcelony sięgają 1899 roku, kiedy to 29 listopada Joan Gamper postanowił wraz z grupą swoich przyjaciół utworzyć klub Foot-Ball Club Barcelona. Choć klub powstał w sercu Katalonii, łatwo zauważyć angielskie pochodzenie jego nazwy. Nie był to przypadek, albowiem zarząd pochodził z Wielkiej Brytanii. Prezesem został Walter Wild, a oprócz niego podpisy na akcie założycielskim złożyli: wspomniany już Joan Gamper (znany też jako Hans Kamper), Lluís d’Ossó, Bartomeu Terradas, Otto Maier, Otto Kunzle, Pedro Cabot, Enric Ducal, Jose Loobet, Carlos Pujol oraz John i William Parsons. Funkcję skarbnika początkowo piastował Bartomeu Terradas, a na sekretarza wybrano Lluísa d’Ossó. Założyciele zdecydowali się na bordowo-granatowe barwy, które były inspirowane klubem z Bazylei. Herbem klubu był po prostu herb miasta.</p>
            <br></br>
            <p>5 grudnia 1899 roku do zarządu dołączyły kolejne osoby. Ogłoszono też kandydata na wiceprezesa, którym został John Parsons. Z kolei William Parsons został członkiem drużyny, w której pełnił funkcję drugiego kapitana.</p>
            <br></br>
            <p>Pierwszy mecz FC Barcelony odbył się 8 grudnia 1899 roku, czyli zaledwie kilka dni po podpisaniu aktu założycielskiego. Na tym iście historycznym spotkaniu Hiszpanie zmierzyli się z Anglikami, którzy pokonali ich 1:0. Konfrontacja miała miejsce na Velodromo de la Bonanova, pierwszym boisku Barcy. Relację z tego meczu można było przeczytać 9 grudnia w dzienniku „La Vanguardia”. W ramach ciekawostki można dodać, że w składzie każdej drużyny znajdowało się wtedy po dziesięciu zawodników.</p>
            <br></br>
            <p>Pierwszy wygrany mecz Barca wpisała na swoje konto 25 grudnia 1899 roku, gdy w spotkaniu z miejscową drużyną Catala FC zwyciężyła 3:1. Jakiś czas później zarząd zdecydował, że klub musi mieć własne boisko, wobec czego wydzierżawiono stadion znajdujący się niedaleko popularnego hotelu Casanovas.</p>
            <p>23 grudnia 1900 roku odbyły się pierwsze derby Katalonii, a FC Barcelona zmierzyła się ze studenckim zespołem Sociedad Española de Futbol. Mecz zakończył się wynikiem 0:0.</p>
            <br></br>
            <p>W 1901 roku Barca wzięła udział w turnieju, w którym puchar fundowany był przez Alfonso Macaya. Zajęła w nim drugie miejsce. Jednak przy okazji kolejnej edycji zwyciężyła cały turniej, tracąc tylko dwie bramki, a strzelając aż sześćdziesiąt.</p>
            <br></br>
            <p>Pierwszy mecz na wyjeździe FC Barcelona rozegrała 13 maja 1902 roku. Jej przeciwnikiem był nowy wówczas klub Madrid FC (dziś znany wszystkim kibicom jako Real Madryt). Spotkanie zakończyło się zwycięstwem Dumy Katalonii.</p>
            <br></br>
            <p>Poza granicami Hiszpanii Barca zagrała po raz pierwszy 1 maja 1904 roku. Był to wyjazd do Francji. Duma Katalonii spotkała się na Stade Olympique z miejscowym zespołem Stade O.E.T, który pokonała 3:2.</p>
            <br></br>
            <p>W 1905 roku FC Barcelona wygrała Puchar Katalonii, w finale zwyciężając z Espanyolem 3:2.</p>
            <br></br>
            <p>Choć Barca odnosiła sukcesy, nie brakowało momentów, w których wszyscy byli przekonani, że klub upadnie. Znaczący wpływ na krytyczną sytuację hiszpańskiego klubu miała malejąca liczba socios, czyli kibiców będących jednocześnie członkami klubu i opłacających specjalne składki. W 1908 roku Barca miała już tylko 38 socios. Wtedy Jaon Gamper przestał być kapitanem klubu, zakończył swoją karierę sportową i dołączył do zarządu klubu. Został w nim prezesem na pięcioletnią kadencję. Gamper sprawił, że sytuacja Barcy bardzo szybko uległa poprawie. Od 14 marca 1909 roku drużyna zaczęła rozgrywać mecze na wykupionym na własność stadionie Camp del Carrer Indústria.</p>
            <br></br>
            <p>W sezonie 1909/1910 Barca po raz kolejny sięgnęła po mistrzostwo Katalonii oraz zdobyła Puchar Hiszpanii. Wobec tak świetnej formy drużyny do klubu zaczęły wpływać kandydatury na socios. W kolejnym sezonie FC Barcelona miała ich już 367.</p>
        </div>
    );
}

export default HistoryPage;